import React from 'react'
import ooo from '../../sketches/motion_arteffact'
import { loadableP5 as P5Wrapper } from '../../sketches/lodable'

export default () => {
  return (
    <>
      <div>
        <h3>Instrucciones: </h3>
        <ul>
          <li>Observe la imagen unos segundos, luego observe una imagen slida</li>
        </ul>

        <p>
          Notar que el estmulo en la imagen slida. Se cree que el efecto secundario del movimiento es el resultado de 
          la adaptacin del movimiento, ya que las neuronas que codifican un movimiento particular reducen sus respuestas
          con el tiempo de exposicin a un estmulo en constante movimiento.
        </p>
      </div>
      <div style={{ textAlign: 'center' }}>
        <P5Wrapper sketch={ooo} />
      </div>
    </>
  )
}
